@import '../variables';

span {
    white-space: pre-line;
}

.login-area {
    border: 1px solid $border-color-gery; border-radius: 14px; position: relative;
}

.login-area .login-top {
    padding: 50px 40px; height: 10%;
}
.login-area .login-middle {
    padding: 0px 30px 50px 60px; display: flex; height: 65%; justify-content: space-between;
}
.login-info-left {
    display: grid; width: 360px;
}
.login-info-left *:nth-child(4) {
    height: 75px;
}
.login-info-left input {
    border: none; border-bottom: 1px solid $border-color-deep;
    font-weight: 600; font-size: 16px; line-height: 19px; letter-spacing: 0.01em; 
}
.login-info-left input:focus {
    outline: none;
}
.login-info-left .login-check-area {
    align-self: center; display: flex;
}
.login-info-left .login-check-area label:last-child {
    padding-left: 16px;
}
.custom-checkbox-label {
    display: flex;
    align-items: center;
  }
  .custom-checkbox {
    display: none;
  }
  .custom-checkbox-icon {
    width: 16px;  height: 16px; border: 2px solid gray; display: flex;
    border-radius: 4px;  background-color: white; border: 1px solid $border-color-gery;
  }
  .custom-checkbox-icon img {
    padding: 25%;
  }
  .custom-checkbox:checked + .custom-checkbox-icon {
    background-color: $primary-color-red; border: 1px solid $primary-color-red;
  }
  
  .custom-checkbox-text {
    margin-left: 8px;
  }
  
.login-info-left .login-otp-area {
    display: inline-grid;
}
.login-info-left .login-btn {
    background-color: $primary-color-red; color: white; border: none; font-weight: 700;
    font-size: 18px; line-height: 21px;  letter-spacing: 0.01em; border-radius: 10px;
}
.login-info-left .login-btn:hover {
    background-color: $primary-color-red-hover;
}
.login-info-right {
     padding: 0 20px; border-left: 1px solid $border-color-deep;
}
.login-info-right ul {
    height: 100%;
}
.login-info-right li {
    padding: 10px; border-bottom: 1px solid $border-color-deep;  height: 25%;
    display: flex;  align-items: center; 
}
.login-info-right li:first-child {
    border-top: 1px solid $border-color-deep;
}
.login-info-right li span {
    width: 80%;
    display: inline-block;  font-weight: 600;
    font-size: 16px;
    line-height: 19px;  letter-spacing: 0.01em;
}
.login-bottom {
    background-color: $board-color-grey; height: 17%; width: 100%;
    line-height: 19px; text-align: center; position: absolute; bottom: 0;  display: grid;
    align-content: space-evenly; border-bottom-left-radius: 14px; border-bottom-right-radius: 14px;
}

.login-bottom span {
    font-weight: 600;
    font-size: 16px; 
}
.login-bottom span:first-child {
    color: $primary-color-red;
}
.login-middle .login-warning {
    align-self: center; letter-spacing: 0.01em; color: $primary-color-red; 
}
.login-security-txt {
    display: flex;     align-items: self-end;
}
.login-security-txt {
    
    img {
        border: 1px solid $border-color-gery;
    }
    .secutiry-txt {
        border: 1px solid $border-color-gery;
    }
}
.security-reload {
    vertical-align: top;  padding: 3px;
}
.radio-btn{
    width: 16px !important;
    height: 16px !important;
}
.otp-btn {
    margin: 10px 0;
}
.otp-checkbox {
    padding: 10px 0;
}
.otp-checkbox div:first-child {
    padding-right: 20px;
}
.otp-checkbox input[type="radio"] {
    accent-color: $primary-color-red;
}
.tmp-otp #user-email{
    width: 100%;
}
.cancel-btn {
    background-color: white !important; color: $primary-color-red !important; border: 2px solid $primary-color-red !important; margin-right: 10px;
}
/* 노트북 & PC (해상도 1024px)*/ 
@media all and (min-width:1024px) {
    .login-background {
        display: flex;
      justify-content: center;
      align-items: center;
      height: 100vh; /* 화면의 높이에 따라 조절 */
    }
  .login-area {
    width: 775px; height: 562px;     
  }
  .login-area div:first-child .welcome-title{
    font-weight: 600; font-size: 28px; line-height: 32px; letter-spacing: 0.01em;
  }
  .security-num{
    width: 144px; height: 44px;
  }
  .secutiry-txt {
     height: 46px; margin-left: 30px;
  }
.login-middle .login-warning {
    font-weight: 400; font-size: 14px;
    line-height: 16px;  
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .modal-title {
    font-weight: 700; font-size: 20px; line-height: 23px; letter-spacing: 0.01em;
  }
  .modal-content {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    width: 350px; min-height: 195px; height: fit-content; 
    display: flex; flex-direction: column;
    .alert-bottom {
        text-align: right; justify-content: end;
    }
    button {
        background-color: $primary-color-red; color: white; font-weight: 600;
        font-size: 14px; line-height: 22px; border-radius: 5px; border: none; width: 97px;
        height: 32px;
    }
    .alert-top {
       position: relative;
       img {
        position: absolute; right: 0;
       }
    }
    .modal-content {
        .alert-middle  {
            flex-grow: 2; padding: 10px;
        }
        span {
            font-weight: 600; font-size: 15px; line-height: 22px; letter-spacing: 0.01em; 
        }
    }
  }

  .alert-middle-txt {
    border: 1px solid $line-color-grey; border-radius: 10px; padding: 10px;
}

  /** pw 오류 경고창 */
 
  /** 회원가입 양식 폼 */
  .modal-join {
    width: 420px; height: 526px; 
    .join-middle {
        flex-grow: 4;
    }
    button {
        width: 100%;
    }
    .join-top {
        text-align: right;
    }
    input,
    .user-type {
        width: 265px; height: 42px; border: 1px solid $border-color-gery;
        border-radius: 5px;
    }
    .join-input >  div {
            padding: 7px 0;
            
    }
    .join-input  div label {
        font-weight: 600;
        font-size: 16px;
        line-height: 17px;
        letter-spacing: 0.025em;
    }
    .user-type *:hover {
        background-color: $hover-color-pink; color: $primary-color-red;
    }
    .alert-middle {
        padding-top: 15px;
    }
    .alert-bottom {
        position: relative;
    bottom: -20px;
    }
    select, 
    input {
        padding: 10px;
    }

    .custom-select {
        width: 265px !important; margin: 0 !important; height: 42px !important;
    }
    .custom-select > div,
    .custom-select > div > div:first-child {
        height: 100%;
    }
    .custom-select > div > div:last-child {
        height: fit-content;
    } 
    .custom-select div:first-child div:first-child div:first-child {
        height: 42px !important; align-items: center;
    }
    
  }

  /** privacy polisy 모달 */
 .modal-privacy {
    .alert-middle {
        padding-top: 10px;
    }
    .alert-middle-info {
        padding: 10px 0;
    }
   
    .pw-checkbox {
        padding: 10px 0;
    }
    .alert-bottom {
        padding: 10px 0;
    }
    button {
        width: 100%; 
    }
   /** 비밀번호 찾기 모달 */
   
 }
  
 .modal-password {
    .alert-middle {
        padding-top: 10px;
    }
    .alert-middle-info {
        padding: 10px 0;
    }
    .alert-middle-txt {
        padding: 10px 10px 20px 10px;
    }
    .pw-checkbox {
        padding: 10px 0;
    }
    button {
        width: 100%; 
    }
   }
 .modal-otp{
    min-height: 300px;
    height: fit-content;
    padding-bottom: 40px;
    .alert-middle-info > div{
        padding: 5px 0;
    }
    .horizon-division {
        width: 100%;
    height: 2px;
    background: $border-color-gery;
    margin: 20px 0;
    }
 }
 
} 

