@import '../variables';

.faq-lists {
    display: flex;
}
.faq-lists li {
     border: 1px solid $border-color-gery; padding :20px 25px; border-radius: 10px;     max-width: 296px; max-height: 130px;
     overflow: hidden;
}
.faq-lists li:not(:last-child) {
    margin-right: 10px; 
}
.faq-lists .faq-top {
    display: flex; margin-bottom: 14px; 
    
}

.faq-lists .faq-top p {
    font-weight: 600;  font-size: 20px; line-height: 23px; letter-spacing: 0.01em;
}
.faq-lists .faq-top p:first-child {
    color: $primary-color-red;
}
.faq-lists .faq-summary {
    font-weight: 400; font-size: 17px; line-height: 23px; color: $txt-color-content;
}
.faq-category {
    padding: 27px 25px 25px 25px;
}
.faq-category-lists {
    display: flex; justify-content: space-between; position: relative; 
    pointer-events: auto; overflow-x: auto;
    li {
        align-self: center; position: relative; min-width: 120px;
        div {
            text-align: center;     padding-bottom: 10px;
        }
    }
    
}
.icon-modal {
    // width: 146px; left: -10px; z-index: 999;
        position: absolute; 
        background-color: white;  width: 227px; min-height: 170px; 
        border-radius: 20px; border: 1px solid $primary-color-red;
        padding-bottom: 10px; z-index: 9999;
        img {
            position: relative;
            top: -13px; left: 50%;
        }
        li {
            padding: 10px 15px;
        }
        .custom-hover {
            border-radius: 10px;
        }
        .no-list{
            display: flex; 
            justify-content: center;
            top: 50px;
            position: relative;
        }
    }
.faq-category-lists::-webkit-scrollbar {
    width: 1px; height: 8px;
}
  
.faq-category-lists::-webkit-scrollbar-thumb {
    background-color: $border-color-gery;
    border-radius: 4px;
}

.faq-category-lists::-webkit-scrollbar-track {
    background-color: #f1f1f1;
}  
.faq-category-lists .icon-modal {
    ul {
        border: 2px solid $primary-color-red; border-radius: 15px; padding: 10px; background-color: white;
        text-align: initial;
    }
}
.faq-category-lists .icon-modal li {
    padding: 5px; font-weight: 400;
    font-size: 18px;
    line-height: 30px;
}
.faq-contents {
    display: flex; justify-content: space-between; overflow: hidden; width: 100%;
    .board-table {
        border-top: 2px solid $primary-color-red;  background: #FBFBFB; 
        padding: 10px 30px; text-align: center;
        li {
            font-size: 16px; font-weight: 400;  padding: 0 5px;
        }
    }
    .board-list {
        border-top: 1px solid #E8E8E8;
        font-size: 15px; font-weight: 400; justify-self: end; 
        ul:not(:nth-child(3)) {
            text-align: center;
        }
        li {
            text-overflow: ellipsis; overflow: hidden; white-space: nowrap;
            & > *{
                padding: 0 5px; text-overflow: ellipsis; overflow: hidden; white-space: nowrap;
            }
        }
        span {
            color: $board-color-txt;
        }
        .col-3  {
           span {
            
            font-size: 16px; font-style: normal; font-weight: 600; line-height: normal; color: #333333;
           }
           img {
            vertical-align: bottom;
           }
        }
        .col-5 img {
            vertical-align: bottom;
        }
        .small-detail {
            margin-top: 2px;
            & > * {
                padding-right: 10px;
            }
            span {
                color: #5F5F5F; font-size: 15px; font-weight: 400;
            }
        }
    }
    .board-list:hover {
        background-color: $hover-color-pink; 
        span {
            color: $primary-color-red;
        }
    }
    .editor-wrapper {
        min-height: 649px;
        max-height: 649px;
    }
}
.faq-right, .faq-left {
    width: 49%;
}
.faq-category-lists p {
    margin: 0; font-weight: 600; text-align: center;
}
.faq-left {
    .faq-count{
        margin-bottom: 15px;
        font-size: 14px;
        line-height: 19px;
        color: #626262;
    }
    .ag-theme-alpine {
        height: 500px;
    }
    .ag-header {
        display: none;
    }
    .ag-center-cols-container {
        height: 100% !important;
        & div {
            height: 10% !important; border-bottom: 1px solid $border-color-gery !important;
        }
    }
    .pagination {
        margin-top: 0 !important; padding-top: 40px;  border-top: 1px solid #E8E8E8;
    }
    .custom-scroll-area {
        max-height: 460px;
        min-height: 460px;
    }
} 
.faq-left::-webkit-scrollbar {
    width: 8px;
  }
  
  .faq-left::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 4px;
  }
  
  .faq-left::-webkit-scrollbar-track {
    background-color: #f1f1f1;
  }  
// board
.faq-custom-board {
//    margin-bottom: 40px;
    li {
        padding: 18px 24px 18px 0px;  display: flex;
        align-items: center;  border-bottom: 2px solid $board-color-grey;  max-height: 54px;
        img { 
            margin-left: 10px;
        }
        span {
            font-weight: 600;
            font-size: 16px; overflow: hidden;
            line-height: 24px; height: 24px;
        }
        span:first-child {
            width: 100px; text-align: center; min-width: 100px; 
        }
        span:last-child {
            margin-left: auto; font-size: 15px; font-weight: 400; line-height: 24px; letter-spacing: 0.01em;
        }
    }
    li:first-child {
        border-top: 1px solid $board-color-grey;
    }
    li:hover {
        background-color: $hover-color-pink;   color: $primary-color-red;
    }
}
.faq-right {
    border-radius: 14px; border: 1px solid $border-color-gery;
    overflow: hidden;
    display: flex;
    flex-direction: column;  transition: transform 0.3s ease;
}
.faq-right .ck-sticky-panel {
    display: none !important;
}
.faq-right-top {
     border-bottom: 1px solid $border-color-gery;
    min-height: 100px; 
    // z-index: 300;
    padding: 15px 30px;
    // position: sticky;
    top: 0;  display: inline-table;
    background: white; box-sizing: content-box;
    & > p {
        padding: 20px 0;  overflow-wrap: break-word;
    }
    span {
        padding: 4px; align-self: center;
    }
    .user-action span img {
        padding-right: 2px;     width: 16px;
        height: 16px;
    }
    
}
.faq-right-bottom {
    margin-top: auto;
}
.faq-attach{
    border: 1px solid $border-color-gery; border-radius: 5px; font-weight: 600;
    font-size: 13px;
}

.faq-attach-down img {
    padding: 0 5px;
}
.faq-comment-wrapper {
    background-color: #F2F2F2;min-height: 103px;
    padding: 16px; height: fit-content;
    & > span:first-child {
        font-weight: 600;
        font-size: 15px;
        line-height: 18px;
    }
    & > div {
        padding: 5px 0;
        display: flex;
    }
    textarea {
        flex-grow: 2;
        border: none;
        padding: 10px;
    }
    textarea:focus {
        outline: none;
    }
    button {
        width: 69px;
        // height: 44px;
        background: #323232;
        border-radius: 5px;
        padding-left: 1px;
        color: white;
        border: none;
    }
    
}
.faq-comment-list {
    padding : 10px 20px; background-color: #F2F2F2;
    .comment-middle {
        overflow-wrap: break-word;
    }
}
.comment-input {
    display: flex;
    flex-direction: column;
    width: 620px;
}
.comment-input span {
    height: 24px;
    background: white;
    border-bottom: 1px solid $border-color-gery; font-weight: 600;
    font-size: 12px;
    line-height: 24px; padding-left: 10px;
} 
.comment-cnt-title {
    font-weight: 600;
    font-size: 14px;
    line-height: 16px; padding-left: 10px;
}
.faq-comment-list > ul > li {
    padding: 10px 0;
}
.faq-comment-list > ul > li:not(:last-child) {
    // border-bottom: 1px solid $line-color-grey;
}
.faq-comment-list > ul > li span:first-child {
    font-weight: 600;
    font-size: 14px;
}
.comment-top div span:first-child {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px; letter-spacing: 0.01em; padding-right: 10px;
}
.comment-top div span:last-child{
    font-weight: 400;
font-size: 12px;
line-height: 24px; letter-spacing: 0.01em;
}
.comment-bottom span {
    font-weight: 600;
font-size: 12px;
line-height: 24px; padding: 0 3px;
}
.faq-comment-list > ul > li .comment-top span p {
    font-weight: 400;
    font-size: 13px;
    color: $txt-color-edit;
}
.faq-comment-list > ul > li span:last-child  p:nth-child(2) {
    padding: 0 10px;
}
/* AG grid custom css*/
.ag-root-wrapper {
    border: none !important;
}

.faq-right {
    overflow: auto; width: 100%;
  }
  
  /* 스크롤바 스타일링 */
  .faq-right::-webkit-scrollbar {
    width: 8px;
  }
  
  .faq-right::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 4px;
  }
  
  .faq-right::-webkit-scrollbar-track {
    background-color: #f1f1f1;
  }  
.faq-contents .pagination li{
    margin: 0 5px;
}
.faq-contents .pagination li a,
.faq-contents .pagination  .disabled {
    color: $chart-color-grey;
}
.faq-contents .pagination .active {
    background-color: white !important; border: 2px solid $primary-color-red; border-radius: 6px;
}
.faq-contents .pagination .active a {
    color: $primary-color-red; font-weight: 600;
}
.faq-contents .ck.ck-editor__main>.ck-editor__editable:not(.ck-focused) {
    border: none;
    padding: 20px;
}
.faq-contents .ck.ck-editor {
    z-index: -1px;
}
/* 노트북 & PC (해상도 1024px)*/ 
@media all and (min-width:1024px) {
    .faq-lists li {
        padding :20px 25px;
   }
   .faq-left .ag-cell:first-child {
        width: 100px !important; 
   }
   .faq-left .ag-cell:last-child {
    position: absolute !important;
    left: 80px !important; width: /*fit-content*/ 90% !important; text-align: left !important;
    }
    .faq-right-top > p {
        font-weight: 700; font-size: 24px; line-height: 30px;     max-width: 600px;
    }
   
} 

/* 테블릿 가로, 테블릿 세로 (해상도 768px ~ 1023px)*/ 
@media all and (min-width:768px) and (max-width:1023px) {
    .faq-lists li {
        padding :20px 25px;
   }
   .faq-left .ag-cell:first-child {
        width: 100px !important;
   }
   .faq-left .ag-cell:last-child {
    position: absolute !important;
    left: 80px !important; width: /*fit-content*/ 90% !important; text-align: left !important;
    }
    .faq-right-top {
        height: 100px; padding: 15px 30px;
    }
    .faq-right-top > p {
        font-weight: 700; font-size: 24px; line-height: 30px;
    }
} 

/* 모바일 가로, 모바일 세로 (해상도 480px ~ 767px)*/ 
@media all and (max-width:767px) {
    .faq-lists li {
        padding :20px 25px;
   }
   .faq-left .ag-cell:first-child {
        width: 100px !important;
   }
   .faq-left .ag-cell:last-child {
    position: absolute !important;
    left: 80px !important; width: /*fit-content*/ 90% !important; text-align: left !important;
    }
    .faq-right-top {
        height: 100px; padding: 15px 30px;
    }
    .faq-right-top > p {
        font-weight: 700; font-size: 24px; line-height: 30px;
    }
}